@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    /* list-style: none; */
    text-decoration: none;
}

/* root applies for entire project */
:root {
    /* --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255,0.4); */

    /* --color-bg: #2E0138;
    --color-bg-variant: #956397;
    --color-primary: #63057C;
    --color-primary-variant: #F7F2F8; */

    --color-bg: #D6BCA0;
    --color-bg-variant:#715546;
    --color-primary: #F07900;
    --color-primary-variant:#D35100;
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: 400ms ease;
}

/* for smooth scrolling */
html {
    scroll-behavior: smooth;
}

/* to disable original scroll */
::-webkit-scrollbar {
    display:none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    /* background: white; */
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/texture.jpg);
}

/* general styles for tags and class */

.container {
    width: var(--container-width-lg);
    margin: auto auto;
}

h1,h2,h3,h4,h5 {
    font-weight: 500;
    /* margin-bottom: 0%;
    margin-top: 0%;
    margin-left: 0%;
    margin-right: 0%; */
}

h1 {
    font-size: 2.5rem;
    margin-bottom: -0.5rem;
    margin-top: -1rem;
    margin-left: 0%;
    margin-right: 0%;
    padding: 0%;
    
}

/* components are wrapped in section tag */
section {
    padding-top: 1rem;
    /* min-height: 100vh; */
    margin-bottom: 5%;
}

/* every section will have h2 & h5 text at the top */
section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 2rem;  
}

.text-light {
    color: var(--color-light);
    /* margin-bottom: -1.5rem; */
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: block;
    /* display:inline-block; */
    /* color: var(--color-primary); */
    color: #fff;
    padding: 0.25rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 2px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background-color: var(--color-white);
    color: var(--color-primary);
    border-color: var(--color-white);
}

.btn-primary {
    background: var(--color-primary) ;
    /* color: var(--color-bg) ; */
    color:var(--color-white);
    transition: var(--transition);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* medium devices */

@media screen and (max-width: 1024px) {
   .container {
    width: var(--container-width-md);
   } 

   section {
    margin-top: 1rem;
    margin-bottom: 10%;
   }
}

/* small devices */

@media screen and (max-width: 600px) {
    .container {
     width: var(--container-width-sm);
    } 

    section {
        margin-top: 0rem;
    margin-bottom: 15%;
       }

    section > h2 {
        margin-bottom: 1rem;  
    }
 }