.about__container{
    display: grid;
    grid-template-columns: 26% 4% 40% 0% 33%;
    /* gap: 4% 3%; */
}


.about__me {
   width: 100%; 
   aspect-ratio: 1/1;
   border-radius: 2rem;
   /* background: linear-gradient(
    45deg,
    transparent,
    var(--color-bg-variant),
    transparent
   ); */
   display: grid;
   place-items: center;
   margin-top: 45%;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(-10deg);
    transition: 1000ms ease;
    width: 60%;

}

.about__me-image:hover{
    transform: rotate(0);
}

.career__objective{
    text-align: center;
}

.other__activities{
    margin-top: 15%;
    text-align: center;
    /* margin-bottom: 15%; */
}

.about__cards {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

