.about__education {
    text-align: center;
    /* background-color: brown; */
}

.about__content p {
    margin: 0rem 0 1rem;
    color: var(--color-light);
    /* background-color: aqua; */
}

.other__activities-mobile {
    display: none;
}

/* medium devices */

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    
    .about__me {
        width: 40%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }

    .about__education {
        margin-top: 2rem;
    }

    .other__activities {
        display: none;
    }
    .other__activities-mobile {
        display: block;
        margin-top: 1.5rem;
        text-align: center;
    }
 }
 
 /* small devices */
 
 @media screen and (max-width: 600px) {
    
    .about__me {
        width: 45%;
        margin: -1rem auto 0rem;
    }

    .about__me-image {
        width: 40%;
    
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        align-items: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
    .about__education {
        margin-top: 2rem;
    }

    .other__activities {
        display: none;
    }

    .other__activities-mobile {
        display: block;
        margin-top: 1.5rem;
        text-align: center;
    }
  }